                                ></v-date-picker>
                            </v-menu>
                        </div>
                        <div class="term"> ~</div>
                        <div class="day-item">
                            <v-menu
                                    ref="menu2"
                                    v-model="menu2"
                                    transition="scale-transition"
                                    offset-y
                                    max-width="290px"
                                    min-width="auto"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                    <v-text-field
                                            v-model="enmonth"
                                            class="day-item"
                                            dense
                                            readonly
                                            suffix="월"
                                            v-bind="attrs"
                                            v-on="on"
                                    ></v-text-field>
                                </template>
                                <v-date-picker
                                        v-model="enmonth"
                                        type="month"
                                        @input="menu2 = false"
                                ></v-date-picker>
                            </v-menu>

                        </div>
                    </div>
                    <div v-else class="year-box">
                        <v-select class="year-item"
                                  v-model="styear"
                                  dense
                                  :items="yearList">

                        </v-select>
                        <span> ~ </span>
                        <v-select class="year-item"
                                  dense
                                  v-model="enyear"
                                  :items="yearList">

                        </v-select>
                    </div>
                    <div class="button-box">
                        <v-btn depressed class="white-button submit-button" @click="search()">
                            <font-awesome-icon icon="fa-solid fa-magnifying-glass" style="margin-right:6px;"/>
