                        </v-btn>
                    </div>
                </div>
                <div style="padding-right:16px;">
                    <v-btn type="button" depressed class="black-button" @click="excelDown()">
                        접속현황 엑셀다운로드
                    </v-btn>
                </div>
            </div>


            <div class="graph-box">
                <div class="graph-title">
                    사용자 방문 현황
                    <span class="date">{{label}}</span>
                </div>
                <v-progress-circular v-if="!chartShow"
                                     :size="100"
                                     class="progress-box"
                                     color="#DD1B16"
                                     indeterminate
                ></v-progress-circular>
                <LineChartGenerator v-if="chartShow"
                                    :chart-options="chartOptions"
                                    :chart-data="chartData"
                                    :height="height"
                />
            </div>
            <div class="graph-box">
                <div class="graph-title">
                    방문자 사용시간
                    <span class="date">{{label}}</span>
                </div>
                <v-progress-circular v-if="!chartShow"
                                     :size="100"
                                     class="progress-box"
                                     color="#DD1B16"
                                     indeterminate
                ></v-progress-circular>
                <LineChartGenerator v-if="chartShow"
                                    :chart-options="timeChartOptions"
                                    :chart-data="timeChartData"
                                    :height="height"
                />
            </div>
            <div class="status-box">
                <div class="left-table">
                    <div class="table-title">
                        사용자 방문 현황
                        <span class="number text-blue">{{memberTotal}}명</span>
                        <span class="date">{{label}}</span>
                    </div>
                    <div class="data-box">
                        <table class="table" cellspacing="0" cellpadding="0">
                            <thead>
                            <tr>
                                <th>날짜</th>
                                <th>전체</th>
                                <th>개인회원</th>
                                <th>기업회원</th>
                                <th>미로그인</th>
                            </tr>
                            </thead>
                            <tbody>
                            <tr v-for="item in inviteList">
                                <td style="max-width:110px;">{{item.date}}</td>
                                <td v-html="item.totalString"></td>
                                <td>{{util.numberFormat(item.indi)}}</td>
                                <td>{{util.numberFormat(item.busi)}}</td>
                                <td>{{util.numberFormat(item.nouser)}}</td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <div class="right-table">
                    <div class="table-title">
                        방문자 사용시간
                        <span class="number text-blue">{{memberTime}}</span>
                        <span class="date">{{label}}</span>
                    </div>
                    <div class="data-box">
                        <table class="table" cellspacing="0" cellpadding="0">
                            <thead>
                            <tr>
                                <th>날짜</th>
                                <th>전체</th>
                                <th>개인회원</th>
                                <th>기업회원</th>
                                <th>미로그인</th>
                            </tr>
                            </thead>
                            <tbody>
                            <tr v-for="item in timeList">
                                <td style="max-width:110px;">{{item.date}}</td>
                                <td>{{item.total}}</td>
                                <td>{{item.indi}}</td>
                                <td>{{item.busi}}</td>
                                <td>{{item.nouser}}</td>
                            </tr>
                            </tbody>
                        </table>

                    </div>
                </div>
            </div>
        </div>
        <div style="display: none;">
            <table border="1" id="excelTable">
                <thead>
                <tr>
                    <th>날짜</th>
                    <th>전체</th>
                    <th>개인회원</th>
                    <th>기업회원</th>
                    <th>미로그인</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="item in inviteList">
                    <td style="text-align:center;">{{item.date}}</td>
                    <td style="text-align:center;" v-html="item.totalString"></td>
                    <td style="text-align:center;">{{util.numberFormat(item.indi)}}</td>
                    <td style="text-align:center;">{{util.numberFormat(item.busi)}}</td>
                    <td style="text-align:center;">{{util.numberFormat(item.nouser)}}</td>
                </tr>
                </tbody>
            </table>
            <table border="1" id="excelTimeTable">
                <thead>
                <tr>
                    <th>날짜</th>
                    <th>전체</th>
                    <th>개인회원</th>
                    <th>기업회원</th>
                    <th>미로그인</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="item in timeList">
                    <td  style="text-align:center;">{{item.date}}</td>
                    <td style="text-align:center;">{{item.total}}</td>
                    <td  style="text-align:center;">{{item.indi}}</td>
                    <td style="text-align:center;">{{item.busi}}</td>
                    <td style="text-align:center;">{{item.nouser}}</td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>


<script>

    import {Line as LineChartGenerator} from 'vue-chartjs/legacy'

    import {
        Chart as ChartJS,
        Title,
        Tooltip,
        Legend,
        LineElement,
        LinearScale,
        CategoryScale,
        PointElement
    } from 'chart.js'

    ChartJS.register(
        Title,
        Tooltip,
        Legend,
        LineElement,
        LinearScale,
        CategoryScale,
        PointElement
    )


    import apiService from '@/config/admin/apiService'
    import {
        API_ADMIN_GET_STATISTICS_MEMBER
    } from '@/config/admin/urls'
    import UTIL from "@/common/util";

    export default {
        components: {
            LineChartGenerator
        },
        data() {
            return {
                util: UTIL,
                chartData: {
                    labels: [],
                    datasets: []
                },
                timeChartData: {
                    labels: [],
                    datasets: []
                },
                chartOptions: {
                    responsive: true,
                    maintainAspectRatio: false
                },
                timeChartOptions: {
                    responsive: true,
                    maintainAspectRatio: false,
                    scales: {
                        x: {
                            ticks: {
                                beginAtZero: false,
                                min: 100,
                            },
                        },
                        y: {
                            ticks: {
                                //  max: 7000,
                                // min: 0,
                                // stepSize: 1000,
                                autoSkip: true
                            },
                        }
                    },
                },
                chartShow: false,
                height: 300,
                dateClass: {
                    year: '',
                    month: '',
                    week: '',
                    day: 'active'
                },
                dateType: 'day',
                dateMenu1: false,
                dateMenu2: false,
                stdate: '',
                endate: '',
                stmonth: '',
                enmonth: '',
                menu1: false,
                menu2: false,
                yearList: [],
                styear: '',
                enyear: '',
                dayList: [],
                label: '',
                timeLabel: '',
                timeList: [],
                inviteList: [],
                memberTotal: 0,
                memberTime: '',
            };
        },
        created() {
            this.setInit();
        },
        methods: {
            setInit() {
                const date = new Date();
                const year = date.getFullYear();
                const day = (date.getDate() < 10) ? '0' + date.getDate() : date.getDate();
                for (let y = year; y >= 2010; y--) {
                    this.yearList.push({text: y + '년', value: y})
                }
                this.styear = year - 5;
                this.enyear = date.getFullYear();

                let month = parseInt(date.getMonth()) + 1;
                if (month < 10) month = '0' + month;


                const predate = new Date(year, parseInt(date.getMonth() - 1), date.getDate());
                const lastdate = new Date(year, date.getMonth(), 0);
                const lastDay = lastdate.getDate();


                let premonth = parseInt(predate.getMonth()) + 1;
                if (premonth < 10) premonth = '0' + premonth;
                const preDay = (date.getDate() > lastDay) ? lastDay : day;

                this.stmonth = predate.getFullYear() + '-' + premonth;
                this.enmonth = year + '-' + month;

                this.stdate = year + '-' + premonth + '-' + preDay;
                this.endate = year + '-' + month + '-' + day;
                this.getData();
            },
            getData() {
                if (!this.setDateList()) return;
                this.chartShow = false;
                const frm = {
                    dateType: this.dateType,
                    styear: this.styear.toString(),
                    enyear: this.enyear.toString(),
                    stmonth: this.stmonth.toString(),
                    enmonth: this.enmonth.toString(),
                    stdate: this.stdate.toString(),
                    endate: this.endate.toString(),
                }
                switch (this.dateType) {
                    case 'day':
                    case 'week':
                        this.label = '[' + this.stdate + ' ~ ' + this.endate + ']';
                        break;
                    case 'month':
                        this.label = '[' + this.stmonth + ' ~ ' + this.enmonth + ']';
                        break;
                    case 'year':
                        this.label = '[' + this.styear + ' ~ ' + this.enyear + ']';
                        break;
                }
                apiService.post(API_ADMIN_GET_STATISTICS_MEMBER, frm, (res) => {
                    this.setGraph(res)
                })
            },
            setDateList() {
                let lastDay;
                let startDay;

                let stNumber = 0;
                let enNumber = 0;
                let diffDate = '';
                switch (this.dateType) {
                    case 'day':
                    case 'week':
                        let stdates = this.stdate.split('-');
                        let stDayDate = new Date(stdates[0], stdates[1] - 1, stdates[2]);
                        let endates = this.endate.split('-');
                        let enDayDate = new Date(endates[0], endates[1] - 1, endates[2]);
                        diffDate = enDayDate.getTime() - stDayDate.getTime();
                        if (diffDate < 0) {
                            let params = {
                                message: '시작일은 종료일보다 같거나 작아야 합니다',

                            }
                            this.$eventBus.$emit('modalOpen', params);
                            return false;
                        }
                        if (this.dateType == 'day') {
                            let diffDateNumber = Math.abs(diffDate / (1000 * 60 * 60 * 24));
                            if (diffDateNumber > 31) {
                                let params = {
                                    message: '일별 통계는 기간 차이가 31일 이내여야 합니다 ',

                                }
                                this.$eventBus.$emit('modalOpen', params);
                                return false;
                            }
                            this.dayList = []
                            let stYear = stDayDate.getFullYear();
                            let enYear = enDayDate.getFullYear();
                            let stMonth = stDayDate.getMonth() + 1;
                            let enMonth = enDayDate.getMonth() + 1;
                            let checkMonthFlag = false;
                            let lastDayDate = new Date(stdates[0], stdates[1], 0);
                            lastDay = lastDayDate.getDate();
                            let startDay = stDayDate.getDate();
                            let endDay = enDayDate.getDate();
                            if (stMonth != enMonth) {
                                for (let k = startDay; k <= lastDay; k++) {
                                    let date = (k < 10) ? '0' + k : k;
                                    let dateMonth = (stMonth < 10) ? '0' + stMonth : stMonth;

                                    this.dayList.push({
                                        keyDate: dateMonth + '/' + date,
                                        viewDate: stYear + '.' + dateMonth + '.' + date,
                                    })
                                }
                                startDay = 1;
                            }
                            for (let k = startDay; k <= endDay; k++) {
                                let date = (k < 10) ? '0' + k : k;
                                let dateMonth = (enMonth < 10) ? '0' + enMonth : enMonth;
                                this.dayList.push({
                                    keyDate: dateMonth + '/' + date,
                                    viewDate: enYear + '.' + dateMonth + '.' + date,
                                })

                            }
                        } else {
                            this.dayList = []
                            let weekText = ['일', '월', '화', '수', '목', '금', '토'];
                            for (let k = 0; k <= 6; k++) {
                                this.dayList.push(weekText[k])
                            }

                        }
                        break;
                    case 'month':
                        stNumber = parseInt(this.stmonth.replace('-', ''));
                        enNumber = parseInt(this.enmonth.replace('-', ''));
                        diffDate = enNumber - stNumber;
                        if (diffDate < 0) {
                            let params = {
                                message: '시작일은 종료일보다 같거나 작아야 합니다',

                            }
                            this.$eventBus.$emit('modalOpen', params);
                            return false;
                        }
                        let stMonths = this.stmonth.split('-');
                        let enMonths = this.enmonth.split('-');
                        let stYear = parseInt(stMonths[0]);
                        let enYear = parseInt(enMonths[0]);
                        let startMonth = parseInt(stMonths[1]);
                        let endMonth = parseInt(enMonths[1]);

                        this.dayList = [];
                        if (diffDate >= 99) {
                            this.monthAllView = true;
                            for (let k = 1; k <= 12; k++) {
                                let date = (k < 10) ? '0' + k : k;
                                this.dayList.push(date)
                            }
                        } else {
                            this.monthAllView = false;
                            if (stYear != enYear) {
                                for (let k = startMonth; k <= 12; k++) {
                                    let date = (k < 10) ? '0' + k : k;
                                    let dateYear = (stYear < 10) ? '0' + stYear : stYear;
                                    this.dayList.push(dateYear + '/' + date)
                                }
                                startMonth = 1;
                            }
                            for (let k = startMonth; k <= endMonth; k++) {
                                let date = (k < 10) ? '0' + k : k;
                                let dateYear = (stYear < 10) ? '0' + stYear : stYear;
                                this.dayList.push(dateYear + '/' + date)

                            }
                        }
                        break;
                    case 'year':
                        if (parseInt(this.styear) > parseInt(this.enyear)) {
                            let params = {
                                message: '시작일은 종료일보다 같거나 작아야 합니다',

                            }
                            this.$eventBus.$emit('modalOpen', params);
                            return false;
                        }
                        lastDay = this.enyear;
                        startDay = this.styear;
                        this.dayList = []
                        for (let k = startDay; k <= lastDay; k++) {
                            let date = (k < 10) ? '0' + k : k;
                            this.dayList.push(date)
                        }
                        break;
                }
                return true;

            },
            setDateType(code) {
                this.dateType = code;
                for (const key in this.dateClass) {
                    if (key == code) this.dateClass[key] = 'active';
                    else this.dateClass[key] = '';

                }
            },
            search() {
                this.getData();
            },
            setDateText(keyDate) {
                let dateText = '';
                switch (this.dateType) {
                    case 'week':
                        let weekText = ['', '일', '월', '화', '수', '목', '금', '토'];
                        dateText = weekText[keyDate];
                        break;
                    case 'month':
                        dateText = keyDate.replace('/', '.');
                        break;

                    default:
                        dateText = keyDate;
                        break;
                }
                return dateText;
            },
            setGraph(res) {

                this.chartData.labels = [];
                this.timeChartData.labels = [];
                this.inviteList = [];
                this.timeList = [];
                const dataKeyset = {};
                const timeDataKeyset = {};
                let indexDateKey = ''
                let memberTotal = 0;
                for (const item of res.memberData) {
                    indexDateKey = this.getIndexDateKey(item)
                    dataKeyset[indexDateKey] = item;
                    memberTotal += item.total;
                }
                this.memberTotal = UTIL.numberFormat(memberTotal);

                let memberTime = 0;
                for (const item of res.memberTimes) {
                    indexDateKey = this.getIndexDateKey(item)
                    timeDataKeyset[indexDateKey] = item;
                    memberTime += item.total;
                }
                this.memberTime = this.secToText(memberTime)

                let dataList = [];
                let indiDataList = [];
                let busiDataList = [];
                let nouserDataList = [];

                let timeDataList = [];
                let indiTimeDataList = [];
                let busiTimeDataList = [];
                let nouserTimeDataList = [];
                let index = 1;
                let keyDate = '';
                let dateText = '';
                for (const item of this.dayList) {
                    if (this.dateType == 'week') {
                        keyDate = index;
                        this.chartData.labels.push(item);
                        this.timeChartData.labels.push(item);

                    } else if (this.dateType == 'day') {
                        keyDate = item.keyDate;
                        this.chartData.labels.push(item.keyDate);
                        this.timeChartData.labels.push(item.keyDate);
                    } else {
                        keyDate = item;
                        this.chartData.labels.push(item);
                        this.timeChartData.labels.push(item);

                    }
                    if (dataKeyset[keyDate]) {
                        dataList.push(dataKeyset[keyDate].total)
                        if (dataKeyset[keyDate].indi) indiDataList.push(dataKeyset[keyDate].indi)
                        else indiDataList.push(0)
                        if (dataKeyset[keyDate].busi) busiDataList.push(dataKeyset[keyDate].busi)
                        else busiDataList.push(0)
                        if (dataKeyset[keyDate].nouser) nouserDataList.push(dataKeyset[keyDate].nouser)
                        else nouserDataList.push(0)

                        dataKeyset[keyDate].totalString = UTIL.numberFormat(dataKeyset[keyDate].total) + '<span style="color:#3949AB;font-size:14px;">[' + UTIL.numberFormat(dataKeyset[keyDate].indi + dataKeyset[keyDate].busi) + ']</span>'
                        if (this.dateType == 'day') {
                            dateText = item.viewDate;
                        } else {
                            dateText = this.setDateText(keyDate);
                        }
                        dataKeyset[keyDate].date = dateText;
                        this.inviteList.push(dataKeyset[keyDate])
                    } else {
                        dataList.push(0)
                        indiDataList.push(0)
                        busiDataList.push(0)
                        nouserDataList.push(0)
                        if (this.dateType == 'day') {
                            dateText = item.viewDate;
                        } else {
                            dateText = this.setDateText(keyDate);
                        }
                        this.inviteList.push({
                            totalString: 0,
                            date: dateText,
                            total: 0,
                            indi: 0,
                            busi: 0,
                            nouser: 0,
                        })


                    }
                    if (timeDataKeyset[keyDate]) {
                        let total = timeDataKeyset[keyDate].total;
                        timeDataList.push(total);
                        let indi = (timeDataKeyset[keyDate].indi) ? timeDataKeyset[keyDate].indi : 0;
                        indiTimeDataList.push(indi)
                        let busi = (timeDataKeyset[keyDate].busi) ? timeDataKeyset[keyDate].busi : 0;
                        busiTimeDataList.push(busi)
                        let nouser = (timeDataKeyset[keyDate].nouser) ? timeDataKeyset[keyDate].nouser : 0;
                        nouserTimeDataList.push(nouser)
                        if (this.dateType == 'day') {
                            dateText = item.viewDate;
                        } else {
                            dateText = this.setDateText(keyDate);
                        }
                        this.timeList.push({
                            date: dateText,
                            total: this.secToText(total),
                            indi: this.secToText(indi),
                            busi: this.secToText(busi),
                            nouser: this.secToText(nouser),
                        })
                    } else {
                        timeDataList.push(0)
                        indiTimeDataList.push(0)
                        busiTimeDataList.push(0)
                        nouserTimeDataList.push(0)
                        if (this.dateType == 'day') {
                            dateText = item.viewDate;
                        } else {
                            dateText = this.setDateText(keyDate);
                        }
                        this.timeList.push({
                            date: dateText,
                            total: '0s',
                            indi: '0s',
                            busi: '0s',
                            nouser: '0s',
                        })
                    }
                    index++;
                }
                this.chartData.datasets = [
                    {
                        label: '전체',
                        backgroundColor: '#DD1B16',
                        data: dataList
                    },
                    {
                        label: '기업회원',
                        backgroundColor: '#00D8FF',
                        data: busiDataList
                    },
                    {
                        label: '개인회원',
                        backgroundColor: '#FFB300',
                        data: indiDataList
                    },
                    {
                        label: '미로그인',
                        backgroundColor: '#41B883',
                        data: nouserDataList
                    },

                ];

                this.timeChartData.datasets = [
                    {
                        label: '전체',
                        backgroundColor: '#DD1B16',
                        data: timeDataList
                    },
                    {
                        label: '기업회원',
                        backgroundColor: '#00D8FF',
                        data: busiTimeDataList
                    },
                    {
                        label: '개인회원',
                        backgroundColor: '#FFB300',
                        data: indiTimeDataList
                    },
                    {
                        label: '미로그인',
                        backgroundColor: '#41B883',
                        data: nouserTimeDataList
                    },
                ]
                this.chartShow = true;
            },
            secToText(sec) {
                sec = parseInt(sec);
                if (sec < 1) return '0s';

                let day = parseInt(sec / (3600 * 24));
                let hour = parseInt((sec % (3600 * 24)) / 3600);
                let min = parseInt(((sec % (3600 * 24)) % 3600) / 60);
                let vsec = parseInt(sec % 60);
                let result = '';
                if (day) {
                    result = day + 'd ';
                }
                if (hour) {
                    result += hour + 'h ';
                } else {
                    if (day) {
                        result += '0h ';
                    }
                }
                if (min) {
                    result += min + 'm ';
                } else {
                    if (day || hour) {
                        result += '0m ';
                    }
                }
                if (vsec) {
                    result += vsec + 's';
                } else {
                    result += '0s';
                }
                return result;
            },
            getIndexDateKey(item) {
                let checkDate;

                switch (this.dateType) {
                    case 'day':
                        checkDate = item.date.split('-')
                        return checkDate[1] + '/' + checkDate[2];
                        break;
                    case 'month':

                        if (this.monthAllView) {
                            return item.date;
                        } else {
                            checkDate = item.viewDate.split('-')
                            return checkDate[0] + '/' + checkDate[1];
                        }
                        break;
                    case 'week':
                    case 'year':
                        return item.date;
                        break;
                }
            },
            excelDown() {
                this.excelDownActive()
            },
            excelDownActive() {
                const wb = window.XLSX.utils.book_new();
                let fileName = '사용자 접속현황';
                switch (this.dateType) {
                    case 'day':
                        fileName = fileName + '[일간_';
                        break;
                    case 'week':
                        fileName = fileName + '[주간_';
                        break;
                    case 'month':
                        fileName = fileName + '[월간_';
                        break;
                        break;
                    case 'year':
                        fileName = fileName + '[연간_';
                        break;
                        break;
                }
                fileName = fileName + this.stdate + '_' + this.endate + '].xlsx';


                let sheetName = '사용자 방문현황';
                let tableId = 'excelTable'
                let workSheet = window.XLSX.utils.table_to_sheet(document.getElementById(tableId));
                // step 3. workbook에 새로만든 워크시트에 이름을 주고 붙인다.
                window.XLSX.utils.book_append_sheet(wb, workSheet, sheetName);

                sheetName = '방문자 사용시간';
                tableId = 'excelTimeTable'
                workSheet = window.XLSX.utils.table_to_sheet(document.getElementById(tableId));
                // step 3. workbook에 새로만든 워크시트에 이름을 주고 붙인다.
                window.XLSX.utils.book_append_sheet(wb, workSheet, sheetName);


                // step 4. 엑셀 파일 만들기
                const wbout = window.XLSX.write(wb, {bookType: 'xlsx', type: 'binary'});
                // step 5. 엑셀 파일 내보내기
                window.saveAs(new Blob([this.s2ab(wbout)], {type: "application/octet-stream"}), fileName);

            },
            s2ab(s) {
                const buf = new ArrayBuffer(s.length); //convert s to arrayBuffer
                const view = new Uint8Array(buf);  //create uint8array as viewer
                for (var i = 0; i < s.length; i++) view[i] = s.charCodeAt(i) & 0xFF; //convert to octet
                return buf;
            },

        }
    }
</script>

